@import 'normalize';
@import 'icons/iconfont__variables';
@import 'icons/iconfont__icons';
@import 'fonts';
@import 'vars';
@import 'mixins';
@import 'baserules';
@import 'placeholders';

.wrapper {
	@extend %flex;
	width: 940px;
	margin: 2rem auto;
}

.form-inp {

	position: relative;
	height: 4.5rem;
	margin-bottom: 2rem;

	input {
		width: 100%;
		height: 100%;
		border-radius: 0.5rem;
		border: 1px solid #C6CCD2;
		
		&[placeholder] {
			color: $font-dark;
			padding-left: 4.5rem;
			font-size: 1em;
		}
	}

	.icon {
		position: absolute;
		color: $font-dark;
		top: 	50%;
		left: 1.5em;
		transform: translateY(-50%);
		font-size: 1em;
	}

}


/* #Form-top*/


%head-wrap {
	position: relative;
	width: 46rem;
	padding: 5.3rem 0;
	border-radius: 0.5rem 0.5rem 0 0;
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
}

.form-left {
	&__head {
		@extend %head-wrap;
		background: url('../img/top-img1.jpg');
	}
}

.form-right {
	&__head {
		@extend %head-wrap;
		background: url('../img/top-img2.jpg');
	}
}

.head__link-already {
	position: absolute;
	top: 	 20px;
	right: 20px;
	color: $font;
	font-size: 14px;
}

.head__link-sign {
	display: block;
	text-align: center;
	color: $font;
	font-size: 2em;
	text-transform: uppercase;
}


.form-main {
	display: flex;
	flex-direction: column;
	background-color: $grey;
	padding: 3rem;
	padding-bottom: 0;

	&__forgot {
		display: block;
		margin-bottom: 2rem;
		text-align: center;
		color: $font-dark;
	}

	&__sign {
		@extend %link;
		background-color: $blue-light;
	}

	&__log-wrap {
		@extend %flex;
	}

	&__log-fb {
		@include one-btn(2rem);
		@extend %link;
		background-color: $blue;
	}

	&__log-tw {
		@include one-btn(2rem);
		@extend %link;
		background-color: $blue-light;
	}
	
	&__or {
		position: relative;
		color: $font-dark;
		text-align: center;
		text-transform: uppercase;
		margin-bottom: 2rem;

		@mixin line {
			content: "";
			position: absolute;
			top: 50%;
			@include calc(width, '50% - 2rem');
			height: 1px;
			background-color: $font-dark;
		}

		&:after {
			@include line;
			right: 0;
		}
	
		&:before{
			@include line;
			left: 0;
		}
	}

	&__checkbox {
		margin-bottom: 2rem;

		input{
			display: none;

			&:checked + label:before {
				content: "\2713";
				font-size: 10px;
				color: #fff;
				text-align: center;
			}
		}

		label {
			display: inline-block;
			cursor: pointer;
			font-size: 14px;
			color: $font-dark;
			vertical-align: middle;
			padding-bottom: 2px;
			
			&:before {
				content: "";
				display: inline-block;
				width: 1.6rem;
				height: 1.4rem;
				vertical-align: top;
				background-color: $green;
				border-radius: 6px;
				margin-right: 1rem;
				padding-bottom: 2px;
			}
		}
	}
}

.form-title {
	position: relative;
}

.form-title {
	@extend %flex;
	margin: -1rem -1rem 2.5rem -1rem;
	&__sign {
		text-transform: uppercase;
		color: $title-color;
	}
}

.link--title {
	position: static;
	color: $blue-light;
}

.log--icon {
	position: relative;
	font-size: 14px;
	
	.icon {
		position: absolute;
		left: 20px;
	}
}

