  .icon.icon--email:before {
    content: ""; }
  .icon.icon--facebook:before {
    content: ""; }
  .icon.icon--lock:before {
    content: ""; }
  .icon.icon--twitter:before {
    content: ""; }

@font-face {
  font-family: "Cabin";
  font-style: normal;
  font-weight: bold;
  src: url("fonts/Cabin/Cabin-SemiBold.ttf") format("truetype"); }

* {
  box-sizing: border-box; }

html {
  font-size: 10px; }

body {
  font: 16px "Cabin", sans-serif;
  font-weight: bold; }

a {
  text-decoration: none; }

.form-main__sign, .form-main__log-fb, .form-main__log-tw {
  display: block;
  width: 100%;
  padding: 1.5rem 0;
  color: #fff;
  text-align: center;
  border-radius: .5rem;
  margin-bottom: 2rem; }

.wrapper, .form-main__log-wrap, .form-title {
  display: flex;
  justify-content: space-between;
  align-items: flex-start; }

.wrapper {
  width: 940px;
  margin: 2rem auto; }

.form-inp {
  position: relative;
  height: 4.5rem;
  margin-bottom: 2rem; }
  .form-inp input {
    width: 100%;
    height: 100%;
    border-radius: 0.5rem;
    border: 1px solid #C6CCD2; }
    .form-inp input[placeholder] {
      color: #B4BBC2;
      padding-left: 4.5rem;
      font-size: 1em; }
  .form-inp .icon {
    position: absolute;
    color: #B4BBC2;
    top: 50%;
    left: 1.5em;
    transform: translateY(-50%);
    font-size: 1em; }

/* #Form-top*/
.form-left__head, .form-right__head {
  position: relative;
  width: 46rem;
  padding: 5.3rem 0;
  border-radius: 0.5rem 0.5rem 0 0;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat; }

.form-left__head {
  background: url("../img/top-img1.jpg"); }

.form-right__head {
  background: url("../img/top-img2.jpg"); }

.head__link-already {
  position: absolute;
  top: 20px;
  right: 20px;
  color: #fff;
  font-size: 14px; }

.head__link-sign {
  display: block;
  text-align: center;
  color: #fff;
  font-size: 2em;
  text-transform: uppercase; }

.form-main {
  display: flex;
  flex-direction: column;
  background-color: #F2F5F8;
  padding: 3rem;
  padding-bottom: 0; }
  .form-main__forgot {
    display: block;
    margin-bottom: 2rem;
    text-align: center;
    color: #B4BBC2; }
  .form-main__sign {
    background-color: #65ABD0; }
  .form-main__log-fb {
    width: calc(50% - 1rem);
    background-color: #3162A2; }
  .form-main__log-tw {
    width: calc(50% - 1rem);
    background-color: #65ABD0; }
  .form-main__or {
    position: relative;
    color: #B4BBC2;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 2rem; }
    .form-main__or:after {
      content: "";
      position: absolute;
      top: 50%;
      width: -moz-calc(50% - 2rem);
      width: -webkit-calc(50% - 2rem);
      width: calc(50% - 2rem);
      height: 1px;
      background-color: #B4BBC2;
      right: 0; }
    .form-main__or:before {
      content: "";
      position: absolute;
      top: 50%;
      width: -moz-calc(50% - 2rem);
      width: -webkit-calc(50% - 2rem);
      width: calc(50% - 2rem);
      height: 1px;
      background-color: #B4BBC2;
      left: 0; }
  .form-main__checkbox {
    margin-bottom: 2rem; }
    .form-main__checkbox input {
      display: none; }
      .form-main__checkbox input:checked + label:before {
        content: "\2713";
        font-size: 10px;
        color: #fff;
        text-align: center; }
    .form-main__checkbox label {
      display: inline-block;
      cursor: pointer;
      font-size: 14px;
      color: #B4BBC2;
      vertical-align: middle;
      padding-bottom: 2px; }
      .form-main__checkbox label:before {
        content: "";
        display: inline-block;
        width: 1.6rem;
        height: 1.4rem;
        vertical-align: top;
        background-color: #24AA98;
        border-radius: 6px;
        margin-right: 1rem;
        padding-bottom: 2px; }

.form-title {
  position: relative; }

.form-title {
  margin: -1rem -1rem 2.5rem -1rem; }
  .form-title__sign {
    text-transform: uppercase;
    color: #000; }

.link--title {
  position: static;
  color: #65ABD0; }

.log--icon {
  position: relative;
  font-size: 14px; }
  .log--icon .icon {
    position: absolute;
    left: 20px; }
